import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class AccountSetting extends Component {
    render() {
        return (
            <div>
               <div className="content-body">

            <div class="container-fluid">
                <div class="row">
                    <div class="col-xl-3 col-md-4">
                        <div class="card settings_menu">
                            <div class="card-header">
                                <h4 class="card-title">Settings</h4>
                            </div>
                            <div class="card-body">
                                <ul>
                                    <li class="nav-item">
                                        <Link to="/settings" class="nav-link">
                                            <i class="la la-user"></i>
                                            <span>Edit Profile</span>
                                        </Link>
                                    </li>
                                    
                                    <li class="nav-item">
                                        <Link to="/security" class="nav-link">
                                            <i class="la la-lock"></i>
                                            <span>Security</span>
                                        </Link>
                                    </li>
                                    <li class="nav-item">
                                        <Link to="/accountSetting" class="nav-link active">
                                            <i class="la la-university"></i>
                                            <span>Linked Account</span>
                                        </Link>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    
                    <div class="col-xl-9 col-md-8">
                        <div class="card">
                            <div class="card-header">
                                <h4 class="card-title">Linked Account</h4>
                            </div>
                            <div class="card-body">
                                <div class="form">
                                    <ul class="linked_account">
                                        <li>
                                            <div class="row">
                                                <div class="col-9">
                                                    <div class="media">
                                                        <span class="mr-3"><i class="fa fa-bank"></i></span>
                                                        <div class="media-body">
                                                            <h5 class="mt-0 mb-1">Bank of America</h5>
                                                            <p>Bank **************5421</p>
                                                        </div>
                                                        <div class="edit-option">
                                                            <Link to="#"><i class="fa fa-eye"></i></Link>
                                                            <Link to="#"><i class="fa fa-pencil"></i></Link>
                                                            <Link to="#"><i class="fa fa-trash"></i></Link>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-3">
                                                    <div class="verify">
                                                        <div class="verified">
                                                            <span><i class="la la-check"></i></span>
                                                            <Link to="#">Verified</Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </li>
                                    </ul>

                                    <div class="mt-5">
                                        <Link to="/addBank" class="btn btn-primary px-4 mr-3">Add Bank
                                            Account</Link>
                                            
                                        {/* <Link to="/addDebit" class="btn btn-success px-4">Add Debit
                                            Account</Link> */}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </div>
        )

    }
}

export default AccountSetting
