import React, { Component } from 'react'

export class Verify3 extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/verify4')
  }
    render() {
        return (
            <div>
      <div className="verification section-padding">
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center">
      <div className="col-xl-5 col-md-6">
        <div className="auth-form card">
          <div className="card-body">
            <form  onSubmit={this.onSubmitHandler} className="identity-upload">
              <div className="identity-content">
                <span className="icon"><i className="fa fa-shield" /></span>
                <h4>We are verifying your ID</h4>
                <p>Your identity is being verified. We will email you once your verification has
                  completed.
                </p>
              </div>
              <div className="upload-loading text-center mb-3">
                <i className="fa fa-spinner fa-spin fa-3x fa-fw" />
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>





            </div>
        )
    }
}

export default Verify3
