import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class BuySell extends Component {
    render() {
        return (
            <div>
<div className="content-body">
  <div className="container-fluid">
    <div className="row">
      <div className="col-xl-5 col-lg-5 col-md-12">
        <div className="card">
          <div className="card-body">
            <div className="buy-sell-widget">
              <ul className="nav nav-tabs">
                <li className="nav-item"><Link className="nav-link active" data-toggle="tab" to="#buy">Buy</Link>
                </li>
                <li className="nav-item"><Link className="nav-link" data-toggle="tab" to="#sell">Sell</Link>
                </li>
              </ul>
              <div className="tab-content tab-content-default">
                <div className="tab-pane fade show active" id="buy" role="tabpanel">
                  <form name="myform" className="currency_validate">

                    <div className="form-group">                    
                      <div className="input-group mb-3">
                        <select name="currency" className="form-control">
                          <option data-display="Bitcoin" value="bitcoin">Bitcoin
                          </option>
                          <option value="litecoin">Litecoin</option>
                        </select>
                        <input type="text" name="usd_amount" className="form-control" defaultValue="0.00212455 BTC" />
                      </div>
                    </div>



                    <div className="form-group">
                      <label className="mr-sm-2">Payment Method</label>
                      <div className="input-group mb-3">
                        <select name="currency" className="form-control">
                          <option data-display="Bitcoin" value="bitcoin" >Card Payment</option>
                        </select>

                      </div>
                    </div>
                    
                    <div className="form-group">     
                    <label className="mr-sm-2">Beneficiary Account </label>
                      <div className="input-group mb-3">
                        <select name="currency" className="form-control">
                          <option data-display="Bitcoin" value="bitcoin">Bank </option>
                          <option value="litecoin">Litecoin</option>
                        </select>
                        <input type="text" name="usd_amount" className="form-control" defaultValue="0000234565" />
                      </div>
                    </div>
                  </form>
                </div>


                <div className="tab-pane fade" id="sell">
                  <form name="myform" className="currency2_validate">
                    <div className="form-group">
                      <label className="mr-sm-2">Currency</label>
                      <div className="input-group mb-3">
                        <select name="currency" className="form-control">
                          <option data-display="Bitcoin" value="bitcoin">Bitcoin
                          </option>
                          <option value="litecoin">Litecoin</option>
                        </select>
                        <input type="text" name="usd_amount" className="form-control" defaultValue="125.00 USD" />
                      </div>
                    </div>
                    <div className="form-group">
                      <label className="mr-sm-2">Payment Method</label>
                      <div className="input-group mb-3">
                        <select name="currency" className="form-control">
                          <option data-display="Bitcoin" value="bitcoin">Bitcoin
                          </option>
                          <option value="litecoin">Litecoin</option>
                        </select>
                        <input type="text" name="usd_amount" className="form-control" defaultValue="125.00 USD" />
                      </div>
                    </div>
                    <div className="form-group">
                      <label className="mr-sm-2">Enter your amount</label>
                      <div className="input-group">
                        <input type="text" name="currency_amount" className="form-control" placeholder="0.0214 BTC" />
                        <input type="text" name="usd_amount" className="form-control" placeholder="125.00 USD" />
                      </div>
                      <div className="d-flex justify-content-between mt-3">
                        <p className="mb-0">Monthly Limit</p>
                        <h6 className="mb-0">$49750 remaining</h6>
                      </div>
                    </div>
                  </form>
                  
                </div>
                <p className="p-4">Note: Exchanges provide highly varying degrees of safety, security, privacy, and control over your funds and information. Perform your own due diligence
         and choose a wallet where you will keep your bitcoin before selecting an exchange.</p>
              </div>
            </div>
          </div>
         

        </div>
      </div>
      
      
      
      
      
      
      <div className="col-xl-7 col-lg-7 col-md-12">
        <div className="card">
          <div className="card-body">
            <div className="buyer-seller">
              
              <div className="table-responsive">
                <table className="table">
                  <tbody>
                    <tr>
                      <td><span className="text-primary">You are selling</span></td>
                      <td><span className="text-primary">0.00254 BTC</span></td>
                    </tr>
                    <tr>
                      <td>Payment Method</td>
                      <td>Bank of America Bank ***********5245</td>
                    </tr>
                    <tr>
                      <td>Exchange Rate</td>
                      <td>0.00212455 BTC</td>
                    </tr>
                    <tr>
                      <td>Fee</td>
                      <td>$28.00 USD</td>
                    </tr>
                    <tr>
                      <td>Total</td>
                      <td>$854.00 USD</td>
                    </tr>
                    <tr>
                      <td>Vat</td>
                      <td>
                        <div className="text-danger">$25.00 USD</div>
                      </td>
                    </tr>
                    <tr>
                      <td> Sub Total</td>
                      <td> $1232.00 USD</td>
                    </tr>
                  </tbody>  
                </table>
              </div>
            </div>
            
          </div>
          <button name="submit" className="btn btn-success btn-block">Exchange  Now</button>
        </div>
      </div>
    
    </div>
  </div>
</div>
</div>
        )
    }
}

export default BuySell
