import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class AddBank extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/verify6')
  }

    render() {
        return (
            <div>

<div className="verification section-padding mb-5">
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center  my-5">
      <div className="col-xl-5 col-md-6">
        <div className="auth-form card">
          <div className="card-header">
            <h4 className="card-title">Link a bank account</h4>
          </div>
          <div className="card-body">
            <form onSubmit={this.onSubmitHandler} className="identity-upload">
              <div className="form-row">
                <div className="form-group col-xl-12">
                  <label className="mr-sm-2">Account Name </label>
                  <input type="text" className="form-control" placeholder="Maria Pascle" />
                </div>
                <div className="form-group col-xl-12">
                  <label className="mr-sm-2">Account number </label>
                  <input type="text" className="form-control" placeholder={364754567896} />
                </div>
                <div className="form-group col-xl-12">
                  <label className="mr-sm-2">Bank Name </label>
                  <input type="text" className="form-control" placeholder="Bank Of Africa "/>
                </div>
                <div className="text-center col-12">
                  <Link to="/accountSetting" className="btn btn-primary mx-2">Back</Link>
                  <button type="submit" className="btn btn-success mx-2">Save</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>




            </div>
        )
    }
}

export default AddBank
