import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import AuthService from "../Services/auth.service";

export class Settings extends Component {

  constructor(props) {
    super(props);
    this.handleUpdate = this.handleUpdate.bind(this);        
    this.onChangeDob = this.onChangeDob.bind(this);
    this.onChangeBvn = this.onChangeBvn.bind(this);
    this.onChangeAddress = this.onChangeAddress.bind(this);
    
    const userProfile = JSON.parse((localStorage.getItem('users'))).data;  
    this.state = {
      first_name: userProfile.first_name,
      last_name: userProfile.last_name,
      email: userProfile.email,
      dob:"",
      bvn:"",
      address: "",
      successful: false,
      message: ""
    };
  }

  onChangeDob(e) {
    this.setState({
      dob: e.target.value
    });
  }
  onChangeBvn(e) {
    this.setState({
      bvn: e.target.value
    });
  }

onChangeAddress(e) {
  this.setState({
    address: e.target.value
  });
}

handleUpdate(e) {
  e.preventDefault();

  this.setState({
    message: "",
    successful: false
   });


  //if (this.checkBtn.context._errors.length === 0) 
    AuthService.postUpdateProfile(
      this.state.dob,
      this.state.bvn,
      this.state.address
    ).then(
      response => {
        this.setState({
          message: "",
          successful: true
        });
      },
      error => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
         successful: false,
         message: resMessage
        });
      }
    );
  
}
    render() {
        return (
           
  <div className="content-body">
    <div className="container-fluid">
      <div className="row">
        <div className="col-xl-3 col-md-4">
          <div className="card settings_menu">
            <div className="card-header">
              <h4 className="card-title">Settings</h4>
            </div>
            <div className="card-body">
              <ul>
                <li className="nav-item">
                  <Link to="/settings" className="nav-link active">
                    <i className="la la-user" />
                    <span>Edit Profile</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/security" className="nav-link">
                    <i className="la la-lock" />
                    <span>Security</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/accountSetting" className="nav-link">
                    <i className="la la-university" />
                    <span>Linked Account</span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-xl-9 col-md-8">
          <div className="row">
            
            <div className="col-xl-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="card-title">Update Personal Information</h4>
                </div>
                <div className="card-body">

                  <form onSubmit={this.handleUpdate} className="personal_validate">
                    <div className="form-row">
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">First Name</label>
                        <input type="text" className="form-control" id="first_name" value={this.state.first_name} readOnly/>
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Last Name</label>
                        <input type="text" className="form-control" id="last_name" value={this.state.last_name} readOnly />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Email</label>
                        <input type="email" className="form-control" value={this.state.email} id="email" readOnly />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Date of birth</label>
                        <input type="text" className="form-control" placeholder="10-10-2019" id="dob"
                         required   value={this.state.dob} onChange={this.onChangeDob}/>
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Bvn</label>
                        <input type="text" className="form-control" placeholder="563456789" id="bvn"
                         required  value={this.state.bvn} onChange={this.onChangeBvn} />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Address</label>
                        <input type="text" className="form-control" placeholder="123, Central Square, Brooklyn" 
                        id="address" required  value={this.state.address} onChange={this.onChangeAddress} />
                      </div>
                      
                    </div>
                    <div className="row">
                      <div className="form-group col-2">
                        <button className="btn btn-success pl-5 pr-5">Update</button>
                      </div>
                      
                      </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
  
    }
        }
  
export default Settings;
